        сЮЖсЯТсЮУсЮ╢сЯЖсЮЕсЮФсЯЛ: 2025,
        сЮвсЯЖсЮЦсЮ╖_сЮБсЯТсЮЫсЮ╕: 'сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ',
        сЮвсЯЖсЮЦсЮ╖: 'сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ сЮвсЯЖсЮЦсЮ╕сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮУсЯБсЯЗсЯФ',
      },
    ],
  }),
  computed: {
    сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮКсЯВсЮЫсЮФсЮ╢сЮУсЮФсЮЙсЯТсЮЕсЮФсЯЛ() {
      return this.сЮАсЮШсЯТсЮЪсЯДсЮД.filter((сЮА) => сЮА.сЮЖсЯТсЮУсЮ╢сЯЖсЮЕсЮФсЯЛ < (new Date()).getFullYear());
    },
    сЮАсЮШсЯТсЮЪсЯДсЮДсЮЯсЯТсЮЪсЮ╢сЮЬсЮЗсЯТсЮЪсЮ╢сЮЬсЮФсЮЕсЯТсЮЕсЮ╗сЮФсЯТсЮФсЮУсЯТсЮУ() {
      return this.сЮАсЮШсЯТсЮЪсЯДсЮД.filter((сЮА) => сЮА.сЮЖсЯТсЮУсЮ╢сЯЖсЮЕсЮФсЯЛ >= (new Date()).getFullYear());
    },
  },
};
</script>
